.owl-nav {
	& > * {
		@include position(absolute, 0, null, 0, null);
		margin: auto 0;
		width: 50px;
		height: 50px;
		display: flex;
		align-items: center;
		justify-content: center;
		background: rgba($white, 0.6) !important;
		color: $black;
		i {
			margin-bottom: 0;
			font-weight: 400;
			font-size: 1.5rem;
		}
	}
	.owl-next {
		right: 0;
	}
}

.owl-carousel {
  .owl-wrapper-outer {
    overflow: inherit
  }
}

.owl-theme {
  .owl-controls {
    margin-top: 45px;
    .owl-page span {
      width: 10px;
      height: 10px;
      margin: 5px
    }  
  }  
}

.circle-nav {
	.owl-nav{
		& > * {
			border-radius: 100%;
		}
	}
}